	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Cooking with Spinach","date":"31 Jan 2006","slug":"/2006-01-31-cooking-with-spinach/","tags":"Y2006"},"html":"<p><a href=\"http://www.amazon.de/exec/obidos/redirect?link_code=as2&amp;path=ASIN/3774216096&amp;tag=papascott-21&amp;camp=1638&amp;creative=6742\"><img src=\"http://images.amazon.com/images/P/3774216096.03._AA_SCTZZZZZZZ_.gif\" border=\"0\" alt=\"blubb\" align=\"left\" /></a> I'm sure he does it just to shock us, but Christopher regularly requests <a href=\"http://www.iglo.de/index.asp?m1=3&amp;m2=0\" title=\"Willkommen bei iglo\">spinach</a> for supper. He didn't learn to like it from his parents, neither of us can stand the stuff. Now the spokesperson for spinach in Germany is not Popeye, but the ex-model and ex-TV personality Verona Pooth (n&eacute;e  Feldbusch), who in commercials said that her spinach is \"the one that goes blubb\" when it cooks, and even put out a <a href=\"http://www.amazon.de/exec/obidos/redirect?link_code=as2&amp;path=ASIN/3774216096&amp;tag=papascott-21&amp;camp=1638&amp;creative=6742\">spinach cookbook</a>. Paris Hilton couldn't have said it any better.</p>"},"next":{"fields":{"title":"Four Things","date":"29 Jan 2006","slug":"/2006-01-29-four-things/"}},"previous":{"fields":{"title":"Bloggers are Often Self-Loving Eccentrics","date":"03 Feb 2006","slug":"/2006-02-03-bloggers-are-often-self-loving-eccentrics/"}}},{"node":{"fields":{"title":"Four Things","date":"29 Jan 2006","slug":"/2006-01-29-four-things/","tags":"Y2006"},"html":"<p>I was afraid I'd end up with this... Thanks to Hal for tagging me with <a href=\"http://halrager.org/WordPress/?p=1834\" title=\"blivet 2.0 &raquo; Four Things\">Four Things</a></p>\n<p><strong>Four jobs I&rsquo;ve had:</strong><br />\nCleaner in a butcher shop<br />\nPolice radio monitor for a major newspaper (night shift)<br />\nReceptionist<br />\nAccounts payable clerk</p>\n<p><strong>Four movies I can watch over and over again:</strong><br />\nCasablanca<br />\nTo Have and Have Not<br />\nRear Window<br />\nFrom Russia with Love (or any James Bond film with Sean Connery)</p>\n<p><strong>Four places I&rsquo;ve lived:</strong><br />\nEureka, CA<br />\nFortaleza, Brazil<br />\nWickenburg, AZ<br />\nKiel, Germany</p>\n<p><strong>Four TV shows I love:</strong><br />\nnone</p>\n<p><strong>Four places I&rsquo;ve vacationed:</strong><br />\nYosemite National Park, CA<br />\nBoundary Waters Canoe Area, MN<br />\nRovinj, Croatia<br />\nLeogang, Austria</p>\n<p><strong>Four of my favorite dishes:</strong><br />\nI'm losing weight, I can't think about that right now</p>\n<p><strong>Four sites I visit regularly:</strong><br />\n<a href=\"http://www.hardballtimes.com/\">The Hardball Times</a><br />\n<a href=\"http://www.parenthacks.com/\">Parent Hacks</a><br />\n<a href=\"http://fistfulofeuros.net/\">A Fistful of Euros</a><br />\n<a href=\"http://www.allesaussersport.de/\">Allesaussersport</a></p>\n<p><strong>Four places I&rsquo;d rather be right now:</strong><br />\nHome with my family (I'm already there)<br />\nIn a sauna<br />\nThe Sonoran Desert<br />\nWatching baseball</p>\n<p><strong>Four bloggers I&rsquo;m tagging:</strong><br />\n<a href=\"http://justcallmemausi.blogspot.com/\">Christina</a><br />\n<a href=\"http://expat-odyssey.blogspot.com/\">Duncan</a><br />\n<a href=\"http://chicagokarl.de/\">Karl</a><br />\n<a href=\"http://www.oeskovic.com/\">Dave</a></p>"},"next":{"fields":{"title":"Mouse Heads","date":"28 Jan 2006","slug":"/2006-01-28-mouse-heads/"}},"previous":{"fields":{"title":"Cooking with Spinach","date":"31 Jan 2006","slug":"/2006-01-31-cooking-with-spinach/"}}},{"node":{"fields":{"title":"Mouse Heads","date":"28 Jan 2006","slug":"/2006-01-28-mouse-heads/","tags":"Y2006"},"html":"<p><img src=\"http://static.flickr.com/18/92075441_53531802ce.jpg\" width=\"500\" height=\"295\" alt=\"Mouse Heads?\" /></p>\n<p>Mouse Heads?!?</p>\n<p>And if you think there's any truth to this map, then you, too, are a mouse head. (via <a href=\"http://www.denkpass.de/dpblog/sig/archives/000731.html\" title=\"Der Denkpass: The way Americans view the world\">Der Denkpass</a>)</p>\n<p><strong>Update:</strong> Actually, we've always thought of Canada as the land of <a href=\"http://www.webtender.com/db/drink/1534\">screaming orgasms</a>. But then again, we've only been to Thunder Bay. I'm not sure if all of Canada is like that.</p>"},"next":{"fields":{"title":"Google Gone","date":"28 Jan 2006","slug":"/2006-01-28-google-gone/"}},"previous":{"fields":{"title":"Four Things","date":"29 Jan 2006","slug":"/2006-01-29-four-things/"}}}],"pathPrefix":"","first":false,"last":false,"index":242,"pageCount":1101}}

